import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Just(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/just.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[3.58, 1.95, -0.05]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere181.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere181_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere181_2.geometry}
          material={materials.outline}
        />
      </group>
    </group>
  );
}
export default Just;
useGLTF.preload("/glossarySelected/just.glb");
